import React from "react";
import { StaticQuery, graphql } from 'gatsby';
import FeaturedBlogs from "../components/FeaturedBlogs";
import Layout from "../components/layout";

// Components
import SEO from "../components/SEO";

export default function Template({
}) {
  return (
    <Layout>
      <SEO
        title=""
      />
      <StaticQuery
        query={graphql`
              query MyQuery {
                  recent: allMarkdownRemark(
                    limit: 5,
                    sort: { order: DESC, fields: [frontmatter___date] }
                  ) {
                    edges {
                        node {
                        id
                        frontmatter {
                            slug
                            title
                            date
                            cover_photo
                        }
                      }
                    }
                  }
                  travel: allMarkdownRemark(
                    limit: 5,
                    sort: { order: DESC, fields: [frontmatter___date] },
                    filter: {frontmatter:{tags:{in:"travel"}} }
                  ) {
                    edges {
                      node {
                        id
                        frontmatter {
                            slug
                            title
                            date
                            cover_photo
                        }
                      }
                    }
                  }
                  music: allMarkdownRemark(
                    limit: 5,
                    sort: { order: DESC, fields: [frontmatter___date] },
                    filter: {frontmatter:{tags:{in:"music"}} }
                  ) {
                    edges {
                      node {
                        id
                        frontmatter {
                            slug
                            title
                            date
                            cover_photo
                        }
                      }
                    }
                  }
              }
          ` }
        render={(data) => (
          <>
            <FeaturedBlogs data={data.recent} title="Featured" tag="allBlogs" />
            <FeaturedBlogs data={data.travel} title="Travel" tag="travel" />
            <FeaturedBlogs data={data.music} title="Music" tag="music" />
          </>
        )} />
    </Layout >
  )
}
